body {
  color: #060b35;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.product-card {
  box-sizing: border-box;
  background-color: #fff;
  flex-direction: column;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  display: flex;
}

.product-card__img {
  width: 160px;
  height: 134px;
  margin-bottom: 16px;
}

.product-card__title {
  margin-bottom: 8px;
  font-size: 12px;
  font-weight: 600;
  line-height: 18px;
}

.product-card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card .product-card__review {
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.product-card .product-card__review__stars {
  display: flex;
}

.product-card .product-card__review__stars .product-card__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.product-card .product-card__review__stars .product-card__star--active {
  background-image: url("star-active.edda4c43.svg");
}

